renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))


/**
 * Animate
 */
const clock = new THREE.Clock()

document.onkeydown = checkKey;

function checkKey(e) {


    e = e || window.event;

    if (e.keyCode == '81') {
        //Q
        y += 0.5
    }

    if (e.keyCode == '87') {
        //Q
        y -= 1.5
    }
}


