document.getElementById('bloodType').addEventListener("click", () => {
    tipper = Math.random(10)/100
})


const tick = () =>
{
    camera.updateProjectionMatrix();

    left = document.getElementById("left").scrollTop

    let right = document.getElementById("right").scrollTop

    let volumeRight = mapRange(right, 0, 385, -36, 4)

    const elapsedTime = clock.getElapsedTime()

    let scrollY = window.scrollY

    let red = mapRange(left, 0, 606, 0, 1)

    let volumeLeft = mapRange(left, 0, 606, -36, 4)

    let light = mapRange(scrollY, 0, 829, 0.2, 0.3)

    let totalVolume = mapRange(scrollY, 0, 829, -36, 4)

    let color7 = new THREE.Color(1- red, 0, 0)
 
    scene.background = color7

    camera.position.x =  scrollY / 2

    // console.log(totalVolume)

    camera.zoom =  light

    const timer = 0.0001 * Date.now();

    // console.log(scrollY)
   

   player.volume.value = volumeLeft;

   player1.volume.value = volumeRight;

   player3.volume.value = totalVolume;

    x = x + 0.001


    // group.rotation.y += 0.01;
    // group.rotation.x += 0.01


    for ( let i = 0, il = array.length; i < il; i ++ ) {

        const array1 = array[ i ];

        array1.rotation.x += rotater * i 
        array1.rotation.y += rotater * i 
        array1.rotation.z += rotater * i




       array1.position.x = 700  * Math.cos( 1 + timer + i * tipper);
       array1.position.y = 700  * Math.sin( 1 + (timer + i / tipper )) ;
    
        array1.scale.x = 1  + light + right/100
        array1.scale.y =  1 + light + right/100
        array1.scale.z = 1 + light + right/100

        array1.scale.y += y  
        array1.scale.x += y
        array1.scale.z += y

       
    } 



    
    

    // scene.background = color7 
 
   
    // camera.position.x = camera.position.x  -= 0.08

    // Update controls
    // controls.update()

    // Render
    renderer.render(scene, camera)

    // Call tick again on the next frame
    window.requestAnimationFrame(tick)
}

tick()